.navbarMain {

    display: flex;
    font-family: "Poppins", sans-serif;
    width: 100%;
    box-shadow: 0px 1px 1px 0px #0000001F;    

    .logo-box{
        width: 17%;
        cursor: pointer;
   
        svg{
            margin: 0;
        }
    }

    .list-box{
        width: 60%;
        text-align: center;
        display: flex;
        justify-content: center;
    }

    // .logo{
    //     width: 100px;
    //     height: 60px;
        
    // }

    .list{
       
        display: flex;
        list-style: none;
        align-items: center;
        cursor: pointer;
        margin: auto;
        
        li{
            font-weight: 500;
            font-family: "Poppins", sans-serif;
            margin-left: 2rem;
            font-size: 16px;
            color: #292D32;
            line-height: 20.05px;

            span{
                background: #3551B51F;
                padding: 6px 12px 6px 12px;
                border-radius: 10px;
                display: flex;
                margin-left: -1rem;

                p{
                    color: #033B61;
                    margin-right: 0.5rem;
                    margin-left: 0.5rem;
                }
            }
        }


    }

    .nav-btn-box{
        width: 20%;
        justify-content: center;
        display: flex;
        margin: auto;
    }

    .nav-btn1{
        border-radius: 11px;
        font-weight: 400;
        font-size: 14px;
        border: 1px solid #033B61;
        padding: 0px 12px 0px 12px ;
        margin: 10px;
        cursor: pointer;
        background: none;
        display: flex;
        height: 34px;

        svg{
            margin: 0;
            margin-right: 0.5rem;
            margin-top: 0.4rem;
        }

        p{
            margin-top: 0.3rem;
            margin-bottom: 0;
        }
    }

    .nav-btn2{
        border-radius: 11px;
        height: 34px;
        font-weight: 400;
        font-size: 14px;
        border: none;
        padding: 6px 20px 6px 20px ;
        margin: 10px;
        cursor: pointer;
        background: #033B61;
        color: white;
    }

   
}