.Homepage{ 

    width: 100%;
    height: 100%;

    .mt{
        margin-top: 4rem;
      }
    

    .landing-box-main{
        width: 100%;
        height: 80vh;
    }

    .landing-box{
        width: 100%;
        display: flex;
    }

    // .landing-box:before {
    //     content: '';
    //     position: absolute;
    //     bottom: 0;
    //     left: 0;
    //     border-radius: 20px;
    //     border-top: 80px solid white;
    //     border-left: 80px solid #F2D3C9;
    //     width: 0;
    //   }

    .landing-box-lext{
        width: 46.6%;
        height: 75vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 4rem;

    }

    .landing-box-right{
        width: 50%;
        height: 70vh;
        justify-content: center;
        align-items: center;
        display: flex;
        padding: 20px;
        padding-top: 6rem;
        
        img{
            object-fit: cover;
            width: 80%;
        }
      
    }

    .landing-bar{
        width: 100%;
        text-align: center;
        height: 200px;
        background: white;
        font-size: 16px;
        margin-top: 5rem;

        h1{
            color: #033B61;
            font-family: "Open Sans", sans-serif;
            font-size: 24px;
            font-weight: 800;
            line-height: 36px;
        }
    }

    .landing-bar-logo{
        display: flex;
        width: 100%;
        justify-content: space-between;
        padding: auto;
    }

    .landing-bar-img{
        padding: 40px;
        width: 200px;
        height: 110px;

        img{
            width: 100px;
            height: 100px;
        }
    }

    .start-btn{
        width: 290px;
        border-radius: 4px;
        height: 48px;
        background: #033B61;
        font-weight: 500;
        font-size: 16px;
        border: none;
        padding: 5px 12px 5px 12px ;
        margin: 10px;
        color: white;
        cursor: pointer;
    }
   
    .start-btn2{
        border: none;
        background: none;
        color: white;

    }

    .text-box{
        width: 75%;
        font-weight: 900;
        font-size: 34px;
        text-align: left;
        font-family: "Montserrat", sans-serif;

        .para{
            width: 110%;
        }
       
    }

    .para{
        font-family: "Fira", sans-serif;
        width: 100%;
        font-size: 18px;
        font-weight: 400;
        color: #798099;
        margin-top: 20px;
        line-height: 30px;
    }

    .para1{
        font-family: "Roboto", sans-serif;
        width: 100%;
        font-size: 18px;
        font-weight: 700;
        color: #6B7094;
        margin-top: 4rem;
        line-height: 24px;
    }

    .soft{
        color: #1F232B;
        font-weight: 1000;
        font-size: 34px;
        font-family: "Montserrat", sans-serif;
        letter-spacing: -0.68px;
        line-height: 44px;
        word-spacing: -4px;
    }

 
    .built{
        font-size: 34px;
        font-weight: 900;
        background: linear-gradient(to left, #0866C6 100%, #1E8DFF 100%);
        -webkit-background-clip: text;
        color: transparent;
        margin-top: 4px;
        margin-bottom: 8px;
        font-family: "Montserrat", sans-serif;
        line-height: 44px;
        letter-spacing: -0.68px;
        word-spacing: -4px;
    }

    .btn-box{
        justify-content: left;
        width: 78%;
        text-align: left;   
        margin-top: 1.5rem;
        margin-bottom: 1rem;
    }

    .search-arrow{

        display: flex;
        justify-content: left;
        width: 75%;

        p{
            font-family: "Work Sans", sans-serif;
            color: #033B61;
            font-size: 16px;
            line-height: 24px;
            font-weight: 400;
        }
    }

    .explore-box-main{
        width: 100%;
        background: #F8F8FF;
        text-align: center;
        padding-top: 10px;

        h1{
            font-family: "Poppins", sans-serif;
            font-size: 29px;
            font-weight: 800;
            line-height: 29px;
            color: #000000;
            margin-top: 1rem;
        }

    }

    .explore-box{
        margin: auto;
        width: 75%;
        justify-content: center;
        display: grid;
        grid-template-columns: repeat(3, 1fr); 
        gap: 30px;
        align-items: center;
        font-size: 16px;
        text-align: center;
        padding: 20px;
        margin-top: 40px;
        margin-bottom: 20px;
    }

    .explore-small-box{
        display: flex;
        background: #FFFFFF;
        box-shadow: 0px 0px 10px 0px #00000080;
        border-radius: 15px;
        padding: 10px;
        padding-top: 2px;
       align-items: center;
       text-align: left;

        h4{
            font-weight: 700;
            line-height: 24px;
            font-size: 24px;
            font-family: "Poppins", sans-serif;
            margin: 0;
        }

        img{
            width: 75px;
            height: 75px;
            padding: 10px;
            border-radius: 50%;
            padding-right: 0;
            //  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        }

        p{
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            margin: 4px;
            padding-left: 4px;
        }
    }

    .explore-small-box-text{
        margin: auto;
    }

    .btn{
        width: 144px;
        border-radius: 4px;
        height: 40px;
        background: #033B61;
        font-weight: 500;
        font-size: 16px;
        border: none;
        padding: 5px 12px 5px 12px ;
        margin: 10px;
        color: white;
        cursor: pointer;
        margin-bottom: 30px;
        
    }

    .btn:hover{
        background: #033B61;
        color: white ;
    }

   

    .tool-box-main{
        width: 100%;
        background: #F8F8FF;
        text-align: center;
        padding-top: 10px;
        margin-top: 60px;

        h1{
            font-family: "Fira", sans-serif;
            font-size: 55px;
            font-weight: 600;
            line-height: 80px;
            color: #033B61;
            margin: 0;
            margin-bottom: 2rem;
        }

        .para1{
            color: #0D6FD2;
            background: #EDEDFF;
            width: 10%;
            padding: 2px;
            margin: auto;
            border-radius: 4px;
            font-size: 16px;
            margin-top: 3rem;
            margin-bottom: 2rem;
        }

        .para{
            margin-top: 0;
        }

    }

    .tool-box{
        margin: auto;
        width: 90%;
        justify-content: center;
        display: flex;
        align-items: center;
        font-size: 16px;
        text-align: center;
        padding: 20px;
        margin-top: 60px;
        margin-bottom: 20px;
    }

    .tool-small-box{
        
        background: #FFFFFF;
        box-shadow: 0px 15px 30px 0px #30364D1A;
        border-radius: 8px;
        padding: 5rem;
        padding-top: 40px;
       align-items: center;
       text-align: center;
       margin: auto;


        h4{
            font-weight: 700;
            line-height: 28px;
            font-size: 20px;
            font-family: "Fira", sans-serif;
            margin: 0;
        }

        
        h3{
            font-weight: 500;
            line-height: 24px;
            font-size: 18px;
            font-family: "Fira", sans-serif;
            margin: 0;
        }

        img{
            width: 75px;
            height: 75px;
            padding: 10px;
            border-radius: 50%;
            padding-right: 0;
            //  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        }

        p{
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            margin: 4px;
            padding-left: 4px;
        }
    }

    .tool-box-btn{
        border: none;
        font-size: 15px;
        font-weight: 500;
        font-family: "Roboto", sans-serif;
        background: none;
        color: #4067EA;
        margin-top: 20px;
    }

    .compare-box-main{
        width: 100%;
        align-items: center;
        text-align: center;

        h1{
            font-weight: 800;
            font-size: 50px;
            margin: 0;
            font-family: "Fira Sans", sans-serif;
            letter-spacing: -2px;
            word-spacing: -2px;
        }

        h2{
            font-family: "Fira Sans", sans-serif;
            font-weight: 700;
            font-size: 32px;
            line-height: 45px;
        }

        span{
            color: #1457FF;
        }


    }

    .compare-box{
        width: 100%;
        display: flex;
        justify-content: center;
        margin-top: 60px;

        hr{
            margin-top: 1.5rem;
            
        }
       
    }


    .compare-box-left{
        width: 40%;
        align-items: center;
        padding: 40px 30px 80px 50px;
        text-align: left;
        margin-right: 30px;
        border: 1px solid #E4E6F1;
        border-radius: 16px;

        h2{
            color: #30364D;
            font-size: 38px;
        }

        .para{
            color: #30364D;
        }

        ul{
            padding: 0;
        }

        li{
            list-style: none;
            padding-left: 0;
            margin-top: 20px;
            display: flex;
            align-items: center;
            font-family: "Roboto", sans-serif;

            svg{
                margin-right: 10px;
            }
        }

    
        

    }

    .compare-box-right{
        width: 40%;
        align-items: center;
        padding: 40px 30px 80px 50px;
        text-align: left;
        border: 1px solid #E4E6F1;
        border-radius: 16px;
        background: #193498;
        color: white;
        box-shadow: 1px 1px 12px 9px #A9AEC3;
        
     


        ul{
            padding: 0;
        }

        li{
            list-style: none;
            padding-left: 0;
            margin-top: 2rem;
            display: flex;
            align-items: center;
            font-family: "Roboto", sans-serif;

            svg{
                margin-right: 10px;
            }
        }

        hr{
            border: 0.5px solid #282556;
        }

        p{
            margin: 0;
        }

    

        .para{
            font-family: "Roboto", sans-serif;
            margin-top: 1rem;
            color: #FFFFFF;
            font-size: 16px;
            font-weight: 400;
        }
    }

    .praise-box{
        width: 90%;
        display: flex;
        margin: auto;
        margin-top: 6rem;
        margin-bottom: 4rem;
        padding: 10px 40px 10px 40px;

        h1{
            font-size: 28px;
            font-weight: 600;
            margin: 0;
            font-family: "Fira Sans", sans-serif;
            margin-top: 1rem;
        }

    }

    .praise-small-box{

        margin: 1.1rem;

        p{
            color: #8E8E8E;
            font-weight: 400;
            font-size: 16px;
            font-family: "Roboto", sans-serif;
            margin-top: 1rem;
            line-height: 32px;
        }
    }

    .box-group{
        h2{
            font-size: 38px;
            font-weight: 700;
            font-family: "Fira Sans", sans-serif;
            color: #1A1A1A;
            letter-spacing: -2px;
            word-spacing: -2px;
            margin: 0;
            margin-top: 0.5rem;
            margin-bottom: 2rem;
        }

        span{
            font-size: 38px;
            font-weight: 700;
            font-family: "Fira Sans", sans-serif;
            color: #4067EA;
        }

        .text{
            font-family: "Inter", sans-serif;
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            color: #798099;
        }

        h3{
            font-size: 21px;
            font-weight: 500;
            font-family: "Fira Sans", sans-serif;
            color: #30364D;
            line-height: 28px;
            margin: 0;
        }


    }

    .wave {
        width: 100%;
      }

      .para-text{
        font-family:  "Roboto", sans-serif;;
        font-size: 16px;
        font-weight: 400;
        line-height: 25px;
        letter-spacing: -0.32px;
        text-align: left;
        margin-bottom: 4rem;
    }

      .wave-box-main{
        width: 100%;
        background: #E6EAFF;
        text-align: center;
        margin-top: -6px;

        .para1{
            padding-top: 4rem;
            margin-top: 0;
        } 

        .span{
            font-size: 50px;
            font-weight: 800;
            font-family: "Fira Sans", sans-serif;
            color: #4067EA;
        }

        .head{
        
            line-height: 60px;
            letter-spacing: -2px;
            word-spacing: -2px;
            font-size: 50px;
            font-weight: 800;
            font-family: "Fira Sans", sans-serif;
            color: #061237;
        }
        
        .para{
            color: #30364D;
        }


     
     } 

     .wave-box{
        width: 90%;
        margin: auto;
        display: flex;
        background: #E6EAFF;
     } 

      .wave-left{
        padding: 20px;
        width: 50%; 
        overflow: hidden;
        padding: 80px 80px 40px 80px;

        img{
            width: 90%; 
        }
        
      }

      .wave-right{
        text-align: left;
        width: 52%;
        margin: auto;
        padding: 40px 80px 40px 80px;
      }

      .match-box{
        width: 100%;
        margin: auto;
        display: flex;
     } 

      .match-right{
        padding: 20px;
        width: 50%; 
        overflow: hidden;
        display: flex;
        justify-content: center;
        margin: auto;
        padding-top: 8rem;
        
      }

      .match-left{
        text-align: left;
        width: 51%;
        padding: 60px;
        margin: auto;
      }

      .reg-box-main{
      
        width: 100%;
        margin-bottom: 8rem ;
        svg{
            margin-top: -6px;
        }
      }

      .reg-box{
        background: #E6EAFF;
        width: 100%;
        margin: auto;
        display: flex;       
     } 

      .reg-left{
        padding: 20px;
        width: 50%; 
        overflow: hidden;
        display: flex;
        justify-content: center;

        img{
            width: 90%; 
            height: 80%;
        }
        
      }

      .reg-right{
        text-align: left;
        width: 42%;
        padding: 40px;
        margin: auto;
      }

      .svg-box{
       
        display: flex;
        justify-content: center ;
        text-align: left;
        margin: 1rem;
        margin-left: 0;

        svg{
      
            margin: auto;
            margin-right: 1rem;
        }

        p{
            font-family: "Inter", sans-serif;
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            color: #798099;
            margin-top: 0.5rem;
            margin-bottom: 1rem;
        }

        h3{
            margin-bottom: 0.5rem;
        }

      }

      .img-box{
        margin: auto;
      }


      .job-alert-box{
        background-color: #28328C;
        padding: 50px 120px  20px 120px;
        margin-bottom: 2rem;
        margin-top: 8rem;
        display: flex;
        justify-content: center;
        color: white;
        font-family: "Poppins", sans-serif;

        .btn{
            background: #0866C6;
            border-radius: 3px;
            height: 42px;
            margin-top: 2px;
        }

      }

      .job-alert-text-box{
        margin-right: 2rem;

        h1{
font-size: 22px;
font-weight: 700;
line-height: 26px;
letter-spacing: 1.1px;
text-align: center;

        }
      }

      
    .login-input {
        height: 42px;
        background: #F8F7F7;
        width: 20%;
        font-family: 'Poppins', sans-serif;
        border-radius: 5px;
        border: 1px solid #DCD7D7;
        margin-top: 0.2rem;
        padding-left: 20px;
        margin-right: 2rem;
      }
     
}

